import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Index from "./pages/Index.js";
import WhoAmI from "./pages/WhoAmI.js";
import MyProjects from "./pages/MyProjects.js";
import MyJourney from "./pages/MyJourney.js";
import Page404 from "./pages/Page404.js";
import "./css/style.min.css";
import "./index.css";

ReactDOM.render(
  <Router>
      <Switch>
        <Route exact path="/" component={Index} />
        <Route exact path="/WhoAmI" component={WhoAmI} />
        <Route exact path="/MyProjects" component={MyProjects} />
        <Route exact path="/MyJourney" component={MyJourney} />
        <Route component={Page404} />
      </Switch>
  </Router>,
  document.getElementById("root")
);
